<template>
  <el-form label-width="100px" class="my-Form">
    <el-form-item label="用户邮箱：">
      <el-input placeholder="请输入用户邮箱"></el-input>
    </el-form-item>
    <el-form-item label="密码：">
      <el-input placeholder="请输入密码" type="password"></el-input>
    </el-form-item>
    <el-form-item label="确认密码：">
      <el-input placeholder="请再次输入密码"  type="password"></el-input>
    </el-form-item>
    <el-form-item label="验证码：">
      <el-input placeholder="请输入验证码"></el-input>
    </el-form-item>
    <button class="next" @click="next">下一步</button>
  </el-form>
</template>

<script>
export default {
  methods:{
    next() {
      this.$emit('next')
    }
  }
}
</script>

<style lang="less" scoped>
  .my-Form {
    width: 359px;
    margin: 38px auto 0 auto;
  }
  .my-Form /deep/ .el-form-item__label{
    color: #1F1E1E;
    font-weight: 500;
  }
  .next {
    width: 360px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: #B90909;
    color: #fff;
    font-size: 14px;
    font-weight: 50px;
    border: 0;
    border-radius: 4px;
    display: block;
    margin: 0 auto;
  }
</style>


