<template>
  <div class="container register-wrap">
    <img class="bg" src="../../images/register/bg.png" alt="">
    <div class="wrap-center">
      <step-header :stepIndex="stepIndex" :stepList="stepList"></step-header>
      <step1 v-if="stepIndex == 0" @next="next"></step1>
      <step2 v-if="stepIndex == 1" @back="back"></step2>
      <step3 v-if="stepIndex == 2"></step3>
    </div>
  </div>
</template>

<script>
import StepHeader from './step-header'
import step1 from './step1'
import step2 from './step2'
import step3 from './step3'
export default {
  components:{
    StepHeader,
    step1,
    step2,
    step3
  },
  computed:{
    
  },
  data() {
    return {
      stepIndex: 0,
      stepList: [
        {
          title: "注册录入",
        },
        {
          title: "邮箱激活",
        },
        {
          title: "激活成功",
        },
      ]
    }
  },
  methods:{
    back() {
      this.stepIndex = 0
    },
    next() {
      this.stepIndex = 1
    }
  }
}
</script>


<style lang="less" scoped>
.register-wrap {
  position: relative;
  .bg {
    width: 100%;
    display: block;
    min-height: calc(100vh - 66px - 100px);
  }
  .wrap-center {
    width: 594px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    padding: 50px 58px 55px 58px;
    box-sizing: border-box;
  }
}
</style>


