<template>
  <div class="step-wrap">
    <div
      class="step-item"
      :class="{ active: index <= stepIndex }"
      v-for="(item, index) in stepList"
      :key="index"
    >
      <img class="icon-index" :src="checkImg(index)" alt="" />
      {{ item.title }}
    </div>
  </div>
</template>

<script>
export default {
  props:{
    stepIndex: String,
    stepList: Array
  },
  data() {
    return {
    };
  },
  methods: {
    checkImg(index) {
      if (index == 0) {
        return require("../../images/register/icon1-2.png");
      }
      if (this.stepIndex > 0 && index == 1) {
        return require("../../images/register/icon2-2.png");
      }
      if (index == 1 && this.stepIndex == 0) {
        return require("../../images/register/icon2-1.png");
      }
      if (this.stepIndex > 1 && index == 2) {
        return require("../../images/register/icon3-2.png");
      }
      if (this.stepIndex < 2 && index == 2) {
        return require("../../images/register/icon3-1.png");
      }
    },
  },
};
</script>

<style lang="less" scoped>
.step-wrap {
  display: flex;
  width: 100%;
  justify-content: space-between;
  .step-item {
    font-size: 12px;
    font-weight: 500;
    color: #939393;
    display: flex;
    align-items: flex-end;
    position: relative;
    .icon-index {
      width: 18px;
      height: 22px;
      display: block;
      margin-right: 13px;
    }
    &::after {
      position: absolute;
      width: 108px;
      height: 1px;
      background: #939393;
      content: "";
      display: block;
      right: -114px;
      top: 50%;
      transform: translate(0, 50%);
    }
    &:last-child::after {
      display: none;
    }
  }
  .active {
    color: #d20505;
  }
  .active::after {
    background-color: #d20505;
  }
}
</style>


