<template>
  <div class="email-wrap">
    <img class="email" src="../../images/register/email.png" alt="">
    <div class="msg">激活邮件已经发送至liulf@youdaitong.cn</div>
    <button class="back-btn" @click="back">返回首页</button>
    <div class="mark-wrap">
      未收到邮件<img class="mark" src="../../images/register/mark.png" alt="">
    </div>
  </div>
</template>

<script>
export default {
  methods:{
    back() {
      this.$emit('back')
    }
  }
}
</script>
<style lang="less" scoped>
.email-wrap {
  padding-top: 56px;
  .email {
    width: 112px;
    display: block;
    margin: 0 auto 55px auto;
  }
  .msg {
    font-size: 17px;
    color: #1F1E1E;
    font-weight: 500;
    text-align: center;
  }
  .back-btn {
    width: 360px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: #B90909;
    color: #fff;
    font-size: 14px;
    font-weight: 50px;
    border: 0;
    border-radius: 3px;
    display: block;
    margin: 17px auto;
  }
  .mark-wrap {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 58px;
    box-sizing: border-box;
    color: #B0B0B0;
    font-size: 12px;
    .mark {
      width: 17px;
      display: block;
      margin-left: 8px;
    }
  }
}
</style>


